.root {
  display: flex;
  width: 100%;
  flex-direction: row;
  color: black;

  .input-wrapper {
    display: flex;
    width: 100%;
    flex-direction: row;
  }

  &.discrepancy {
    .input-wrapper {
      > :nth-child(1) {
        > :not(:first-child) {
          > :nth-child(1) {
            background-color: hsl(34, 91%, 77%) !important;
          }
        }
      }
      .icon-container {
        font-size: 18px;
        height: 25px;
        width: 25px;
        margin-left: 5px;
        margin-top: 28px;
        margin-bottom: auto;
        padding: 3.4px;
        border-radius: 50%;
        background-color: hsl(34, 91%, 77%);
        color: red;
        // margin: auto;
        display: flex;
        cursor: pointer;
      }
    }
  }
}

.message {
  padding: 10px !important;
  width: 100%;
  max-width: 400px;
}
