.root {
  display: flex;
  width: 100%;
  flex-direction: row;

  .input-wrapper {
    display: flex;
    width: 100%;
    flex-direction: row;
  }

  &.discrepancy {
    .input-wrapper {
      div {
        > div > div > div:first-child {
          // .autocomplete-field{
          background-color: hsl(34, 91%, 77%) !important;
          // }
        }
      }
    }
    .icon-container {
      font-size: 18px;
      height: 25px;
      width: 25px;
      position: relative;
      margin-top: 10px;
      margin-bottom: auto;
      padding: 3.4px;
      border-radius: 50%;
      background-color: hsl(34, 91%, 77%);
      color: red;
      display: flex;
      cursor: pointer;
    }
  }
  .spacer {
    font-size: 18px;
    height: 25px;
    width: 25px;
    position: relative;
    margin-top: 10px;
    margin-bottom: auto;
  }
}

.message {
  padding: 10px !important;
  width: 100%;
  max-width: 400px;
}
